/*****************************
* Normalize CSS
*****************************/
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased; }

.workspace {
  position: relative; }
  .workspace #ex {
    position: absolute;
    background-color: rgba(200, 200, 200, 0.6);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    top: 25vh;
    text-align: center;
    line-height: 50px;
    font-family: 'Encode Sans Expanded', sans-serif;
    font-size: 14px;
    left: -10vw; }

/*****************************
* Head Styles
*****************************/
.head {
  width: 100vw;
  margin-bottom: 60px; }
  .head .title {
    padding-left: 400px;
    padding-top: 20px;
    width: 50vw;
    display: inline-block; }
  .head .title #github-icon {
    width: 40px;
    height: 40px;
    -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
    animation: heartbeat 1.5s ease-in-out infinite both; }
  .head .title h1 {
    display: inline-block;
    padding-left: 20px;
    font-family: 'Encode Sans Expanded', sans-serif;
    font-weight: 100;
    font-size: 40px; }

 /*****************************
* Animation Hearbeat 
*****************************/
@-webkit-keyframes heartbeat {
  from {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: center center;
    transform-origin: center center;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out; }
  10% {
    -webkit-transform: scale(0.91);
    transform: scale(0.91);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in; }
  17% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out; }
  33% {
    -webkit-transform: scale(0.87);
    transform: scale(0.87);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in; }
  45% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out; } }
@keyframes heartbeat {
  from {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: center center;
    transform-origin: center center;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out; }
  10% {
    -webkit-transform: scale(0.91);
    transform: scale(0.91);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in; }
  17% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out; }
  33% {
    -webkit-transform: scale(0.87);
    transform: scale(0.87);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in; }
  45% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out; } }
/*****************************
* Animation Hearbeat end
*****************************/
/*****************************
* Colors up top
*****************************/
#colors {
  width: 50vw;
  height: 70px;
  padding-top: 40px;
  padding-left: 80px;
  margin-top: 0;
  display: inline-block;
  float: right; }
  #colors ul {
    padding-top: 0;
    margin-top: 0; }
  #colors ul li {
    display: inline-block;
    list-style: none;
    width: 20px;
    height: 20px;
    background-color: black;
    padding-left: 20px;
    margin-left: 40px;
    border-radius: 50%;
    text-align: center; }
  #colors #theme2 {
    background-color: #8bd612; }
  #colors #theme3 {
    background-color: #0becec; }

/*****************************
* Main workspace
*****************************/
.workspace {
  width: 60vw;
  margin: 20px auto; }
  .workspace .edit {
    display: inline-block;
    float: left; }
  .workspace textarea {
    resize: none;
    width: 30vw;
    height: 500px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    padding: 20px;
    font-size: 15px;
    border: 0px solid black; }
  .workspace textarea:focus, .workspace input:focus {
    outline: none; }
  .workspace .markdown {
    height: 500px;
    width: 30vw;
    background-color: rgba(200, 234, 234, 0);
    display: inline-block;
    float: right;
    overflow: scroll;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    padding: 20px;
    font-size: 15px; }

/*****************************
* Footer Styles
*****************************/
.footer {
  padding-top: 20px;
  text-align: center;
  letter-spacing: .15em;
  font-family: 'Encode Sans Expanded', sans-serif;
  font-weight: 100;
  font-size: 14px; }

.footer a {
  text-decoration: none;
  color: #2c2ca8;
  font-weight: bolder;
  font-size: 20px;
  border-bottom: 5px solid yellow; }

.color-change-2x {
  -webkit-animation: color-change-2x 10s ease-out infinite alternate-reverse both;
  animation: color-change-2x 10s ease-out infinite alternate-reverse both; }

/*****************************
* Background color change
*****************************/
@-webkit-keyframes color-change-2x {
  0% {
    background: #19dcea; }
  100% {
    background: #b22cff; } }
@keyframes color-change-2x {
  0% {
    background: #19dcea; }
  100% {
    background: #b22cff; } }
/*****************************
* The end 
*****************************/



